import React, { useEffect, useState } from "react";
import StatCards from "../..//views/dashboard/shared/StatCards";

import {
  Paper,
  Box,
  Typography,
  useTheme,
  styled,
  Grid,
  Tooltip,
  Icon,
  Card,
  Fab,
} from "@mui/material";
import appStore from "app/mobxStore/AppStore";
import taskStore from "app/mobxStore/TaskStore";
import { useNavigate } from "react-router-dom";
import StatCards2 from "../..//views/dashboard/shared/StatCards2";
import { Themecolors } from "api/Colors";
import { InfoBox } from "app/appComponents/HeaderBox";
import { UserTypes } from "app/types/User";
import { api } from "api/API";
import Show from "app/appComponents/Show";
import DashBoardListComponent from "app/views/dashboard/shared/DashBoardListComponent";

const H3 = styled("h3")(({ theme }) => ({
  fontSize: "1.25rem",
  fontWeight: 500,
  marginBottom: "12px",
  color: theme.palette.text.primary,
}));

const Span = styled("span")(() => ({
  fontSize: "0.875rem",
  color: "rgba(0, 0, 0, 0.6)",
}));

const Dashboard: React.FC = () => {
  const theme = useTheme();
  const navigate = useNavigate();
  const [userTasks, setUserTasks] = useState<Record<string, any>>({});
  const [taskCounts, setTaskCounts] = useState(appStore.loginResponse.tasks);
  const [pendingTasks, setPendintTasks] = useState(
    appStore.loginResponse.pendingTasks
  );
  const userType = appStore.loginResponse.user[0].userType;
  const [recentData, setRecentData] = useState([]);
  const [applicaitonOldData, SetApplicaitonOldData] = useState([]);

  const loadAssignedTasks = async (type: string, taskIds: number[]) => {
    try {
      const response = await api.post(`assigned-tasks`, {
        body: {
          taskIds: taskIds,
        },
      });
      if (type == "old") SetApplicaitonOldData(response.data);
      else if (type == "new") {
        setRecentData(response.data);
      }
    } catch (err) {
      console.error("Error fetching data:", err);
    }
  };
  useEffect(() => {
    const userId = appStore.loginResponse.user[0].userId;
    let selectedEntityId = appStore.selectedEntity;

    if (userType === UserTypes.DEFAULT_USER) {
      // Assume API is fetching assigned tasks grouped by documentGroupName and documentTypeName
      api.get(`assigned-tasks/user/${userId}`).then((res) => {
        if (res.status) setUserTasks(res.data);
      });
    } else {
      if (appStore.selectedEntity === -1) {
        api
          .get(`get-user-entities/${appStore.loginResponse.user[0].userId}`)
          .then((res) => {
            if (res.success) {
              if (res.data.length > 0) {
                selectedEntityId = res.data[0]?.entityId ?? -1;
              }
              const entites = res.data;
              appStore.setSelectedEntity(selectedEntityId);
              appStore.setUserEntities(entites ?? []);
              loadDashboard(userId, selectedEntityId);
            }
          });
      } else {
        loadDashboard(userId, selectedEntityId);
      }
    }
  }, []);

  const loadDashboard = (userId, selectedEntityId) => {
    api
      .post(`user-dashboard`, {
        body: {
          userId: userId,
          entityId: selectedEntityId,
        },
      })
      .then((res) => {
        if (res.success) {
          setPendintTasks(res.data.pendingTasks);
          setTaskCounts(res.data.tasks);
          loadAssignedTasks("old", res.data.oldestTaskIds);
          loadAssignedTasks("new", res.data.recentTaskIds);
        }
      });
  };
  const handleTaskClick = (group: string, type: string) => {
    const tasks = pendingTasks[group][type];
    if (tasks.length === 1) {
      navigate(`/user/task/${tasks[0]}`);
    } else {
      taskStore.setTasks(tasks);
      navigate("/user/tasks");
    }
  };

  const handleSelect = (item: any) => {
    navigate(`/user/task/${item?.id}`);
  };

  return (
    <Paper
      sx={{
        height: "100%",
        width: "100%",
        p: 2,
        backgroundColor: theme.palette.background.paper,
      }}
    >
      <Box
        sx={{
          display: "flex",
          flexDirection: { xs: "column", md: "row" },
          height: "100%",
          gap: 2,
        }}
      >
        <Box
          sx={{
            flexBasis: { xs: "100%", md: "30%" },
            display: "flex",
            flexDirection: "column",
            gap: 2,
            maxHeight: { md: "calc(100vh - 150px)" },
            overflow: "auto",
          }}
        >
            <Card sx={{ p: 1 }}>
            <DashBoardListComponent
              onButtonClick={handleSelect}
              heading="Recent applications"
              items={recentData}
            />
          </Card>
            <Card sx={{ p: 1, mt: 1 }}>
            <DashBoardListComponent
              onButtonClick={handleSelect}
              items={applicaitonOldData}
              heading="Old applications"
            />
          </Card>
        </Box>

        <Box
          sx={{
            flexGrow: 1,
            p: 1,
            display: "flex",
            flexDirection: "column",
            gap: 2,
            overflow: "auto",
          }}
        >
          <Show condition={userType !== UserTypes.DEFAULT_USER}>
            <Paper
              sx={{
                borderRadius: 2,
                display: "flex",
                p: 1,
                width: "100%",
              }}
            >
              <StatCards2
                onTime={taskCounts.onTime.length}
                Critical={taskCounts.critical.length}
                handleCardClick={(type) => {
                  if (type === "OnTime") {
                    taskStore.setTaskIds(taskCounts.onTime, "On Time");
                  } else {
                    taskStore.setTaskIds(
                      taskCounts.critical,
                      "Priority Applications"
                    );
                  }
                  navigate("/user/tasks");
                }}
              />
            </Paper>
          </Show>

          <Show condition={userType === UserTypes.DEFAULT_USER}>
            <Paper
              sx={{
                p: 2,
                borderRadius: 2,
                width: "100%",
                mt: 2,
              }}
            >
              <H3 sx={{ color: Themecolors.DB_text2 }}>Tasks</H3>
              {Object.entries(userTasks).length > 0 ? (
                <StatCards
                  cardList={userTasks}
                  handleTasks={(tasks) => {
                    if (tasks.length == 1) {
                      navigate(`/user/task/${tasks[0].id}`);
                    } else {
                      const taskIds = tasks.map((t) => t.id);
                      taskStore.setTaskIds(taskIds, "Your Applications");
                      navigate("/user/tasks");
                    }
                  }}
                />
              ) : (
                <InfoBox>
                  <Span>No Tasks Assigned</Span>
                </InfoBox>
              )}
            </Paper>
          </Show>
          <Show condition={userType !== UserTypes.DEFAULT_USER}>
            <Paper
              sx={{
                p: 2,
                borderRadius: 2,
                width: "100%",
                mt: 2,
              }}
            >
              <H3>All Applications</H3>
              {pendingTasks && Object.keys(pendingTasks).length > 0 ? (
                Object.entries(pendingTasks).map(([groupName, types]) => (
                  <Box key={groupName} sx={{ width: "100%" }}>
                    <Typography
                      variant="h6"
                      sx={{ color: theme.palette.primary.main }}
                    >
                      {groupName}
                    </Typography>
                    <Grid container spacing={2} wrap="wrap">
                      {Object.entries(types).map(
                        ([typeName, taskIds]: any[], index) => (
                          <Grid
                            item
                            key={index}
                            sx={{ flexGrow: 1, width: "auto" }}
                            onClick={() => {
                              taskStore.setTaskIds(
                                taskIds,
                                `${groupName} - ${typeName}`
                              );
                              navigate("/user/tasks");
                            }}
                          >
                            <StyledCard elevation={8}>
                              <ContentBox>
                                <Icon className="icon">task_alt</Icon>
                                <Box ml="16px">
                                  <small>{typeName}</small>

                                  <Heading>Tasks: {taskIds.length}</Heading>
                                </Box>
                              </ContentBox>
                              <Tooltip title="View Details" placement="top">
                                <FabIcon
                                  size="medium"
                                  sx={{
                                    background: "rgba(9, 182, 109, 0.15)",
                                  }}
                                >
                                  <Icon>arrow_right_alt</Icon>
                                </FabIcon>
                              </Tooltip>
                            </StyledCard>
                          </Grid>
                        )
                      )}
                    </Grid>
                  </Box>
                ))
              ) : (
                <InfoBox>
                  <Span>No Pending Tasks</Span>
                </InfoBox>
              )}
            </Paper>
          </Show>
        </Box>
      </Box>
    </Paper>
  );
};

export default Dashboard;

const Heading = styled("h6")(({ theme }) => ({
  margin: 0,
  marginTop: "4px",
  fontSize: "18px", // increased font size
  fontWeight: "600",
  color: Themecolors.DB_text2, // use secondary color for highlights
}));

export const FabIcon = styled(Fab)(({ theme }) => ({
  width: "40px !important",
  height: "40px !important",
  boxShadow: "none !important",
  transition: theme.transitions.create("transform"),
  "&:hover": {
    transform: "scale(1.1)",
    backgroundImage: Themecolors.DB_hv1,
  },

  color: Themecolors.DB_Icon2,
}));

const ContentBox = styled(Box)(({ theme }) => ({
  display: "flex",
  flexWrap: "wrap",
  alignItems: "center",
  "& small": {
    fontSize: "16px", // increased font size
    color: Themecolors.DB_text2,
  },
  "& .icon": {
    opacity: 0.8,
    fontSize: "48px", // increased icon size
    color: Themecolors.DB_bg1, // slightly darker shade for icons
  },
}));

export const StyledCard = styled(Card)(({ theme }) => ({
  display: "flex",
  flexWrap: "wrap",
  alignItems: "center",
  justifyContent: "space-between",
  padding: "24px !important",
  cursor: "pointer", // make it selectable
  background: "rgb(244 246 248)", // lighter background for contrast
  transition: "transform .2s", // smooth transition for hover effect
  "&:hover": {
    transform: "scale(1.02)",
    backgroundImage: Themecolors.DB_hv3,
  },
  [theme.breakpoints.down("sm")]: { padding: "16px !important" },
}));
